<template>
    <div class="all_com">
        <div class="title_com"></div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span></span>
            </div>
            <el-form id='form' ref="form" label-position='top' :rules='rules' :model="form" label-width="80px">
                <el-form-item label="" prop='Type'>
                    <el-radio :disabled="canEdit['Type']||!editId?false:true" v-model="form.Type" label="0"></el-radio>
                    <el-radio :disabled="canEdit['Type']||!editId?false:true" v-model="form.Type" label="1"></el-radio>
                </el-form-item>
                <el-form-item label="" prop='InStock'>
                    <el-input :disabled="canEdit['InStock']||!editId?false:true" type='number' v-model="form.InStock"></el-input>
                </el-form-item>
                <el-form-item label="" prop='OfflineStock'>
                    <el-input :disabled="canEdit['OfflineStock']||!editId?false:true" type='number' v-model="form.OfflineStock"></el-input>
                </el-form-item>
                <el-form-item label="" prop='Weight'>
                    <el-input :disabled="canEdit['Weight']||!editId?false:true" type='number' v-model="form.Weight"></el-input>
                </el-form-item>
                <el-form-item label="" prop='Card'>
                    <el-input :disabled="canEdit['Card']||!editId?false:true"  v-model="form.Card"></el-input>
                </el-form-item>
                <el-form-item label="" prop='CardAmount'>
                    <el-input :disabled="canEdit['CardAmount']||!editId?false:true"  v-model="form.CardAmount"></el-input>
                </el-form-item>
                <el-form-item label="" prop='PutAwayTime'>
                    <el-date-picker :disabled="canEdit['PutAwayTime']||!editId?false:true" value-format='timestamp' v-model="form.PutAwayTime" type="datetime" placeholder="" align="right">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label='["/static/img/product/new.jpg"]' prop='Image'>
                    <el-input :disabled="canEdit['Image']||!editId?false:true" v-model="form.Image"></el-input>
                </el-form-item>
                <el-form-item label="" prop='Name'>
                    <el-input :disabled="canEdit['Name']||!editId?false:true" v-model="form.Name"></el-input>
                </el-form-item>
                <el-form-item label="" prop='EnglishName'>
                    <el-input :disabled="canEdit['EnglishName']||!editId?false:true" v-model="form.EnglishName"></el-input>
                </el-form-item>
                <el-form-item label="" prop='Description'>
                    <el-input :disabled="canEdit['Description']||!editId?false:true" v-model="form.Description"></el-input>
                </el-form-item>
                <el-form-item label="" prop='EnglishDescription'>
                    <el-input :disabled="canEdit['EnglishDescription']||!editId?false:true" v-model="form.EnglishDescription"></el-input>
                </el-form-item>
                <el-form-item v-if='form.Type == 1' label="" prop='PresellPrice'>
                    <el-input :disabled="canEdit['PresellPrice']||!editId?false:true" type='number' v-model="form.PresellPrice"></el-input>
                </el-form-item>
                <el-form-item v-if='form.Type == 1' label="" prop='PresellUsdPrice'>
                    <el-input :disabled="canEdit['PresellUsdPrice']||!editId?false:true" type='number' v-model="form.PresellUsdPrice"></el-input>
                </el-form-item>
                <el-form-item label="" >
                    <el-input :disabled="canEdit['OriPrice']||!editId?false:true" type='number' v-model="form.OriPrice"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input :disabled="canEdit['OriUsdPrice']||!editId?false:true" type='number' v-model="form.OriUsdPrice"></el-input>
                </el-form-item>
                <el-form-item label="" prop='Price'>
                    <el-input :disabled="canEdit['Price']||!editId?false:true" type='number' v-model="form.Price"></el-input>
                </el-form-item>
                <el-form-item label="" prop='UsdPrice'>
                    <el-input :disabled="canEdit['UsdPrice']||!editId?false:true" type='number' v-model="form.UsdPrice"></el-input>
                </el-form-item>
                <el-form-item label="" prop='DeliveryTime'>
                    <el-date-picker :disabled="canEdit['DeliveryTime']||!editId?false:true" value-format='timestamp' v-model="form.DeliveryTime" type="datetime" placeholder="" align="right">
                    </el-date-picker>
                </el-form-item>

                <el-form-item label="" prop='Details'>
                    <div id='cn-editor' style="width:1024px;height:500px;"></div>
                    <el-input type='hidden' v-model="form.Details"></el-input>
                </el-form-item>
                <el-form-item label="" prop='EnglishDetails'>
                    <div id='en-editor' style="width:1024px;height:500px;"></div>
                    <el-input type='hidden' v-model="form.EnglishDetails"></el-input>
                </el-form-item>
                <el-form-item label="" prop='AttrName'>
                    <el-input :disabled="canEdit['AttrName']||!editId?false:true" v-model="form.AttrName"></el-input>
                </el-form-item>
                <el-form-item label="" prop='AttrEnglishName'>
                    <el-input :disabled="canEdit['AttrEnglishName']||!editId?false:true" v-model="form.AttrEnglishName"></el-input>
                </el-form-item>
                <el-form-item label="" prop='SoldOutTime'>
                    <el-date-picker :disabled="canEdit['SoldOutTime']||!editId?false:true" value-format='timestamp' v-model="form.SoldOutTime" type="datetime" placeholder="" align="right">

                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop='PreviewTime'>
                    <el-date-picker :disabled="canEdit['PreviewTime']||!editId?false:true" value-format='timestamp' v-model="form.PreviewTime" type="datetime" placeholder="" align="right">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" prop='MinLimit'>
                    <el-input :disabled="canEdit['MinLimit']||!editId?false:true" type='number' v-model="form.MinLimit"></el-input>
                </el-form-item>
                <el-form-item label="" prop='SellType'>
                    <el-select :disabled="canEdit['SellType']||!editId?false:true" v-model="form.SellType" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                        <el-option label="" value="3"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop='ActiveType'>
                    <el-select v-model="form.ActiveType" placeholder="">
                        <el-option label="" value= 0></el-option>
                        <el-option label="" value= 1></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-if='!editId' @click="onSubmit"></el-button>
                    <el-button type="primary" v-else @click="onEdit"></el-button>
                    <el-button></el-button>
                </el-form-item>
            </el-form>
        </el-card>

    </div>
</template>
<script>
import "../../../dist/utf8-jsp/ueditor.config";
import "../../../dist/utf8-jsp/ueditor.all";
import "../../../dist/utf8-jsp/lang/zh-cn/zh-cn";
export default {
    name: "",
    data() {
        var validatePass = (rule, value, callback) => {
            if (value === "") {
                callback(
                    new Error(
                       "!"
                    )
                );
            } else if (value<0) {
                callback(
                    new Error(
                        "!"
                    )
                );
            } else {
                callback();
            }
        };
        return {
            form: {
                Type: "0"
            },
            editId: "",
            canEdit: {
                // 'Weight':true,
                // 'Name':true,
                // 'EnglishName':true,
                EnglishDescription: true,
                Description: true,
                AttrEnglishName: true,
                AttrName: true,
                Card:true,
                CardAmount:true,
                OriUsdPrice:true,
                OriPrice:true
            },
            cnUe: "",
            enUe: "",
            rules: {
                Type: [
                    { required: true, message: "", trigger: "blur" }
                ],
                InStock: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                OfflineStock: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                PutAwayTime: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                Weight: [
                    { required: true, validator: validatePass, trigger: "blur" }
                ],
                Image: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                Name: [
                    { required: true, message: "", trigger: "blur" }
                ],
                EnglishName: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                CardAmount:[
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                Card:[
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                Description: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                EnglishDescription: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                PresellPrice: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                PresellUsdPrice: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                Price: [
                    { required: true, validator: validatePass, trigger: "blur" }
                ],
                UsdPrice: [
                    {
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }
                ],
                DeliveryTime: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                Details: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                EnglishDetails: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                AttrName: [
                    { required: true, message: "", trigger: "blur" }
                ],
                AttrEnglishName: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                SoldOutTime: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                PreviewTime: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                MinLimit: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                SellType: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                ActiveType: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    watch: {
        '$route' (to, from) {
            this.$router.go(0);
        }
    },
    methods: {
        onSubmit() {
            this.$refs.form.validate(valid => {
                if (!valid) {
                    return;
                }
                let params = {
                    notice: ""
                };
                Object.assign(params, this.form);
                params.PutAwayTime = params.PutAwayTime / 1000;
                params.DeliveryTime = params.DeliveryTime / 1000;
                params.SoldOutTime = params.SoldOutTime / 1000;
                params.PreviewTime = params.PreviewTime / 1000;
                this.$api("/admin/product/add", params).then(res => {
                    if (res.code == 0) {
                        this.$router.push("/productmanage/product_list");
                    }
                });
            });
        },
        onEdit() {
            this.$refs.form.validate(valid => {
                if (!valid) {
                    return;
                }
                let params = {
                    notice: "",
                    product_id: this.editId,
                    attr_id : this.form.DefaultAttr.Id,
                    Description: this.form.Description,
                    EnglishDescription: this.form.EnglishDescription,
                    Details: this.form.Details,
                    EnglishDetails: this.form.EnglishDetails,
                    AttrName: this.form.AttrName,
                    OriPrice:this.form.OriPrice,
                    OriUsdPrice:this.form.OriUsdPrice,
                    Card:this.form.Card,
                    CardAmount:this.form.CardAmount,
                    AttrEnglishName: this.form.AttrEnglishName,
                    ActiveType: this.form.ActiveType
                }; 
                this.$api("/admin/product/update", params).then(res => {
                    if (res.code == 0) {
                        this.$router.push("/productmanage/product_list");
                    }
                });
            });
        },
        getDetail(id) {
            let params = {
                product_id: id
            };
            this.$api("/admin/product/info", params).then(res => {
                let params = res.data;
                params.PutAwayTime = params.PutAwayTime * 1000;
                params.DeliveryTime = params.DeliveryTime * 1000;
                params.SoldOutTime = params.SoldOutTime * 1000;
                params.PreviewTime = params.PreviewTime * 1000;
                params.Type = params.IsPresell?'1':'0';
                params.AttrName = params.DefaultAttr.Name;
                params.AttrEnglishName = params.DefaultAttr.EnglishName;
                
                delete params.DefaultAttr.Name;
                delete params.DefaultAttr.EnglishName;
                this.$nextTick(() => {
                this.cnUe.ready(() => {
                this.cnUe.setContent(params.Details);
                })
                this.enUe.ready(() => {
                this.enUe.setContent(params.EnglishDetails);
                })
                })
                Object.assign(params, params.DefaultAttr);
                 Object.assign(params, params.Card);
                this.form = params;
                // console.log(this.form)
            });
        }
    },
    destroyed() {
        // editor

        this.cnUe.destroy();
        this.enUe.destroy();
    },
    mounted() {
        this.$nextTick(() => {
            this.cnUe = UE.getEditor("cn-editor", {
                BaseUrl: "",
                
            });
            this.enUe = UE.getEditor("en-editor", {
                BaseUrl: "",
               
            });
            this.cnUe.addListener("contentChange", () => {
                this.form.Details = this.cnUe.getContent();
                // console.log(this.form.Details);
            });
            this.enUe.addListener("contentChange", () => {
                this.form.EnglishDetails = this.enUe.getContent();
            });
            if (!this.$route.query.id) {
                this.cnUe.ready(() => {
                    //
                    this.cnUe.setContent(
                        `<div id="desProductDetail"> <br/></div><h3 style='font-size:22px'>   </h3><br><div id="desProductParam">         <br> </div><h3 style='font-size:22px'></h3><br><div id="desProductPay">         <br> </div><h3 style='font-size:22px'>
</h3>
<br>
<div id="desProductAfterBought">         <br> 
</div>
<h3 style='font-size:22px'>
    
</h3>`,
                        true
                    );
                });
                this.enUe.ready(() => {
                    //
                    this.enUe.setContent(
                        `<div id="desProductDetail">
    <br/>
</div>
<h3 style='font-size:22px'>
    Notes
</h3>
<br>
<div id="desProductParam">         <br> 
</div>
<h3 style='font-size:22px'>
    Specifications
</h3>
<br>
<div id="desProductPay">         <br> 
</div>
<h3 style='font-size:22px'>
    Payment
</h3>
<br>
<div id="desProductAfterBought">         <br> 
</div>
<h3 style='font-size:22px'>
    Warranty
</h3>`,
                        true
                    );
                });
            }
        });
        if (this.$route.query.id) {
            this.editId = this.$route.query.id;
            this.getDetail(this.editId);
        }
    }
};
</script>
<style lang='scss' scoped>
@import "../../assets/css/titlecom.scss";
.detaila:hover {
    color: #409eff;
    text-decoration: underline;
}
.search_fun {
    height: 60px;
    position: relative;
    top: 10px;
    float: right;
}
.search_input {
    width: 200px;
}
</style>


